import React from "react";
import "flowbite";
import "../style/tailwindCustome.css";
import "../style/style.css";
import hen1 from "../aset/foto/hen4.jpg";
import hen2 from "../aset/foto/hen2.jpg";
import { Link } from "react-scroll";
import animasi from "aos";

export default function Jumbotronn() {
  animasi.init();
  return (
    <>
      <section class="bg-white dark:bg-gray-900 JARAK-ATAS5">
        <div class="gap-16 items-center py-8 px-4 mx-auto max-w-screen-xl lg:grid lg:grid-cols-2 lg:py-16 lg:px-6">
          <div
            data-aos="fade-right"
            data-aos-anchor-placement="bottom-bottom"
            data-aos-duration="2000"
          >
            <h1 class="TEXT-2 dark:text-white">Hallo,,</h1>
            <h1 class="TEXT-1 dark:text-white">i'm Heinricov</h1>
            <p>
              thank you for visiting my website, I am a frontend developer of
              websites and apps with experience with frameworks such as
              Boostrap, TailwindCss, React and many others.
            </p>

            <div className="JARAK-1">
              <button
                type="button"
                class="text-blue-700 hover:text-white border border-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-blue-500 dark:text-blue-500 dark:hover:text-white dark:hover:bg-blue-500 dark:focus:ring-blue-800"
              >
                <Link
                  to="contact"
                  spy={true}
                  smooth={true}
                  offset={-100}
                  duration={500}
                >
                  Contact
                </Link>
              </button>
              <button
                type="button"
                class="text-blue-700 hover:text-white border border-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-blue-500 dark:text-blue-500 dark:hover:text-white dark:hover:bg-blue-500 dark:focus:ring-blue-800"
              >
                <Link
                  to="portfolio"
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                >
                  Portfolio
                </Link>
              </button>
            </div>
          </div>

          <div class="grid grid-cols-2 gap-4 mt-8">
            <img
              class="w-full rounded-lg"
              src={hen1}
              alt="office content 1"
              data-aos="flip-left"
              data-aos-easing="ease-out-cubic"
              data-aos-duration="2000"
            />
            <img
              class="mt-4 w-full lg:mt-10 rounded-lg"
              src={hen2}
              alt="office content 2"
              data-aos="flip-left"
              data-aos-easing="ease-out-cubic"
              data-aos-duration="2000"
            />
          </div>
        </div>
      </section>
    </>
  );
}
