import React, { Component } from "react";
import "flowbite";
import "../style/style.css";
import animasi from "aos";

export default class Judul extends Component {
  render() {
    animasi.init();
    return (
      <h3
        class="JUDUL dark:text-white"
        data-aos="flip-up"
        data-aos-duration="4000"
      >
        {this.props.judul}
      </h3>
    );
  }
}
