import React from "react";
import SosialList from "../components/SosialList";
import Pesan from "../components/Pesan";

export default function ContactKomponen() {
  return (
    <>
      <div id="contact">
        <section class="bg-white dark:bg-gray-900">
          <div class="py-8 lg:py-16 px-4 mx-auto max-w-screen-md">
            <h2 class="mb-4 text-4xl tracking-tight font-extrabold text-center text-gray-900 dark:text-white">
              Contact Me
            </h2>
            <p class="mb-8 lg:mb-16 font-light text-center text-gray-500 dark:text-gray-400 sm:text-xl">
              You can contact me if you need me to complete your project, you
              can send a message directly from the form below or by clicking on
              the social media account link below.
            </p>
            <div>
              <SosialList />
              <Pesan />
            </div>
          </div>
        </section>
      </div>
    </>
  );
}
