import React, { Component } from "react";
import Judul from "../components/Judul";
import "../style/style.css";
import hen4 from "../aset/foto/hen4.jpg";
import ListBio from "../components/ListBio";
import animasi from "aos";

export default class AboutKomponen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      judul: "about",
    };
  }

  render() {
    animasi.init();
    return (
      <>
        <div id="about" className="KONTENER">
          <div className="DISPLAY-FLEX-CENTER">
            <Judul judul={this.state.judul} />
          </div>
          <section class="bg-white dark:bg-gray-900">
            <div class="gap-8 items-center py-8 px-4 mx-auto max-w-screen-xl xl:gap-16 md:grid md:grid-cols-2 sm:py-16 lg:px-6">
              <img
                class="w-full dark:hidden"
                src={hen4}
                alt="dashboard image"
                data-aos="zoom-in-up"
                data-aos-duration="4000"
              />
              <img
                class="w-full hidden dark:block"
                src={hen4}
                alt="dashboard image"
                data-aos="zoom-in-up"
                data-aos-duration="4000"
              />
              <div class="mt-4 md:mt-0" data-aos="zoom-out-left">
                <h2 class="mb-4 text-4xl tracking-tight font-extrabold text-gray-900 dark:text-white">
                  Heinricov Abraham Diego Latupapua.
                </h2>
                <p class="mb-6 font-light text-gray-500 md:text-lg dark:text-gray-400">
                  started learning website programming since 2019, studied
                  independently with the help of social media and free training,
                  born in Batam city on June 29 1993, I am the first child of
                  two siblings, my family comes from the Maluku tribe in Ambon
                  city, now I work in a private company in Jakarta, Indonesia.
                </p>
                <ListBio />
              </div>
            </div>
          </section>
        </div>
      </>
    );
  }
}
